<template>
  <div>
    <i class="el-icon-setting" @click="setThisCom(item)" v-if="iconShow"></i>
    <div>
      组件1
    </div>

    <el-dialog title="dataSet" :visible.sync="dialogFormVisible" v-if="dialogFormVisible" append-to-body>
      <el-form :model="myForm" ref="myForm" label-width="100px">
        <el-form-item label="width"
                      prop="width"
                      :rules="rules">
          <el-input v-model.number="myForm.width" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="height"
                      prop="height"
                      :rules="rules">
          <el-input v-model.number="myForm.height" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="top"
                      prop="top"
                      :rules="rules">
          <el-input v-model.number="myForm.top" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="left"
                      prop="left"
                      :rules="rules">
          <el-input v-model.number="myForm.left" auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="overflow"
                      prop="overflow"
                      :rules="[{required:true,message: '必须输入'}]">
          <el-select v-model="myForm.overflow" placeholder="请选择">
            <el-option value="hidden"></el-option>
            <el-option value="auto"></el-option>
            <el-option value="scroll"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="sureSetCom(myForm)">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import store from '@/store'

  export default {
    props: ['item'],
    data: function () {
      return {
        iconShow: this.$route.path === '/dragAndResize',
        rules: [
          {type: 'number', message: '必须为数字值'}
        ],
        comId: '',//组件id
        dialogFormVisible: false,//显示dialog
        myForm: {
          overflow: 'hidden'
        },//表单
        formLabelWidth: '120px',
        chartData: {
          columns: ['日期', '访问用户', '下单用户', '下单率'],
          rows: [
            {'日期': '1/1', '访问用户': 1393, '下单用户': 1093, '下单率': 0.32},
            {'日期': '1/2', '访问用户': 3530, '下单用户': 3230, '下单率': 0.26},
            {'日期': '1/3', '访问用户': 2923, '下单用户': 2623, '下单率': 0.76},
            {'日期': '1/4', '访问用户': 1723, '下单用户': 1423, '下单率': 0.49},
            {'日期': '1/5', '访问用户': 3792, '下单用户': 3492, '下单率': 0.323},
            {'日期': '1/6', '访问用户': 4593, '下单用户': 4293, '下单率': 0.78}
          ]
        }
      }
    },
    computed: {
      dragAndResizeArr() {
        return store.getters.dragAndResizeArr
      }
    },
    created() {

    },
    methods: {
      setThisCom(item) {
        this.comId = item.id
        this.dialogFormVisible = true
        for (var i in this.dragAndResizeArr) {
          if (this.dragAndResizeArr[i].id === item.id) {
            this.myForm.height = Number(this.dragAndResizeArr[i].objStyle.height.replace('px', ''))
            this.myForm.width = Number(this.dragAndResizeArr[i].objStyle.width.replace('px', ''))
            this.myForm.top = Number(this.dragAndResizeArr[i].objStyle.top.replace('px', ''))
            this.myForm.left = Number(this.dragAndResizeArr[i].objStyle.left.replace('px', ''))
            this.myForm.overflow = this.dragAndResizeArr[i].objStyle.overflow
          }
        }
      },
      sureSetCom(myForm) {
        this.$refs['myForm'].validate((valid) => {
          if (valid) {
            this.dialogFormVisible = false
            this.item.colors = myForm.colors.split(',')
            for (var i in this.dragAndResizeArr) {
              if (this.dragAndResizeArr[i].id === this.comId) {
                this.dragAndResizeArr[i].objStyle.height = myForm.height + 'px'
                this.dragAndResizeArr[i].objStyle.width = myForm.width + 'px'
                this.dragAndResizeArr[i].objStyle.top = myForm.top + 'px'
                this.dragAndResizeArr[i].objStyle.left = myForm.left + 'px'
                this.dragAndResizeArr[i].objStyle.overflow = myForm.overflow
              }
            }
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .el-icon-setting {
    position: absolute;
    top: 5px;
    right: 25px;
    z-index: 999;
    &:hover {
      cursor: pointer !important;
    }
  }
</style>



