<template>
  <div>
    <i class="el-icon-setting" @click="setThisCom(item)" v-if="iconShow"></i>
    <ve-chart :data="chartData"
              :judge-width="true"
              :events="chartEvents"
              :settings="item.options.chartSettings"
              :colors="item.options.colors"></ve-chart>
    <el-dialog title="dataSet" :visible.sync="dialogFormVisible" v-if="dialogFormVisible" append-to-body>
      <el-form :model="myForm" ref="myForm" label-width="100px">
        <form-public :myForm="myForm"></form-public>
        <el-form-item label="colors"
                      prop="colors"
                      :rules="[
                      {required:true,message: '必须输入'},
                      { pattern: /^(#([0-9a-fA-F]{6},?|[0-9a-fA-F]{3},?))+$/g, message: '请按要求输入' }
                      ]">
          <el-input v-model="myForm.colors" auto-complete="off" placeholder="输入标准色号以英文逗号隔开.例:#ddd,#956328"></el-input>
        </el-form-item>
        <el-form-item label="xAxis"
                      prop="xAxis">
          <el-input v-model="myForm.xAxis" auto-complete="off" placeholder="输入xAxis以英文逗号隔开.例:一月,二月"></el-input>
        </el-form-item>
        <el-form-item label="api"
                      prop="api">
          <el-input v-model="myForm.api" auto-complete="off" placeholder="api"></el-input>
        </el-form-item>
        <el-form-item label="type"
                      prop="type"
                      :rules="[{required:true,message: '必须输入'}]">
          <el-select v-model="myForm.type" placeholder="请选择">
            <el-option value="line"></el-option>
            <el-option value="histogram"></el-option>
            <el-option value="pie"></el-option>
            <el-option value="bar"></el-option>
            <el-option value="ring"></el-option>
            <el-option value="radar"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="clickFunction"
                      prop="clickFunction">
          <el-input
            type="textarea"
            :rows="10"
            placeholder="请输入内容"
            v-model="myForm.clickFunction">
          </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="sureSetCom(myForm)">确 定</el-button>
      </div>
    </el-dialog>
    <!--<el-dialog title="dataList" :visible.sync="dialogTableVisible" append-to-body>-->
    <!--<el-table :data="dataList">-->
    <!--<el-table-column property="name" label="name"></el-table-column>-->
    <!--<el-table-column property="value" label="value"></el-table-column>-->
    <!--</el-table>-->
    <!--</el-dialog>-->

  </div>
</template>

<script>
  import store from '@/store'
  import axios from 'axios'

  export default {
    props: ['item'],
    data() {
      const self = this
      this.chartEvents = {
        click: function (e) {
          // const obj = {
          //   dialogVisible: true,
          //   dataList: [{
          //     name: e.seriesName,
          //     value: e.value
          //   }]
          // }
          // self.$store.dispatch('setDialogDataActions', obj)

          //   将上面的代码去除标注后使用
          eval(self.item.options.clickFunction)

          // 以下代码是使用在页面中直接定义dialog的
          // self.dialogTableVisible = true
          // self.dataList = [{
          //   name: e.seriesName,
          //   value: e.value
          // }]
        }
      }
      return {
        dataList: [],
        baseUrl: 'https://www.easy-mock.com/mock/5ba196944442b90220354ed2/yeyoo',
        iconShow: this.$route.path === '/dragAndResize',
        rules: [
          {type: 'number', message: '必须为数字值'}
        ],
        comId: '',//组件id
        dialogFormVisible: false,//显示dialog
        dialogTableVisible: false,//
        myForm: {
          overflow: 'hidden',
          type: 'line'
        },//表单
        formLabelWidth: '120px',
        chartData: {
          columns: ['日期', '访问用户', '下单用户', '下单率'],
          rows: [
            {'日期': '1/1', '访问用户': 1393, '下单用户': 1093, '下单率': 0.32},
            {'日期': '1/2', '访问用户': 3530, '下单用户': 3230, '下单率': 0.26},
            {'日期': '1/3', '访问用户': 2923, '下单用户': 2623, '下单率': 0.76},
            {'日期': '1/4', '访问用户': 1723, '下单用户': 1423, '下单率': 0.49},
            {'日期': '1/5', '访问用户': 3792, '下单用户': 3492, '下单率': 0.323},
            {'日期': '1/6', '访问用户': 4593, '下单用户': 4293, '下单率': 0.78}
          ]
        }
      }
    },
    computed: {
      dragAndResizeArr() {
        return store.getters.dragAndResizeArr
      }
    },
    mounted() {
      this.getData()
    },
    methods: {
      getData() {
        if (this.item.api) {
          axios.get(this.baseUrl + this.item.api).then(res => {
            this.chartData = res.data
          })
        }
      },
      setThisCom(item) {//点击设置的时候显示数据
        this.comId = item.id
        this.dialogFormVisible = true
        this.myForm.border = item.objStyle.border
        this.myForm.clickFunction = item.options.clickFunction
        for (let i in this.dragAndResizeArr) {
          if (this.dragAndResizeArr[i].id === item.id) {
            this.myForm.height = Number(this.dragAndResizeArr[i].objStyle.height.replace('px', ''))
            this.myForm.width = Number(this.dragAndResizeArr[i].objStyle.width.replace('px', ''))
            this.myForm.top = Number(this.dragAndResizeArr[i].objStyle.top.replace('px', ''))
            this.myForm.left = Number(this.dragAndResizeArr[i].objStyle.left.replace('px', ''))
            this.myForm.overflow = this.dragAndResizeArr[i].objStyle.overflow
            this.myForm.colors = this.dragAndResizeArr[i].options.colors.join(',')
            this.myForm.api = this.dragAndResizeArr[i].api
            this.myForm.id = this.dragAndResizeArr[i].id
            this.myForm.type = this.dragAndResizeArr[i].options.chartSettings.type
          }
        }
      },
      sureSetCom(myForm) {
        this.$refs['myForm'].validate((valid) => {
          if (valid) {
            this.dialogFormVisible = false
            this.item.options.colors = myForm.colors.split(',')
            this.item.api = myForm.api
            this.item.objStyle.border = myForm.border
            this.item.options.chartSettings.type = myForm.type
            this.item.options.clickFunction = myForm.clickFunction
            for (let i in this.dragAndResizeArr) {
              if (this.dragAndResizeArr[i].id === this.comId) {
                this.dragAndResizeArr[i].objStyle.height = myForm.height + 'px'
                this.dragAndResizeArr[i].objStyle.width = myForm.width + 'px'
                this.dragAndResizeArr[i].objStyle.top = myForm.top + 'px'
                this.dragAndResizeArr[i].objStyle.left = myForm.left + 'px'
                this.dragAndResizeArr[i].objStyle.overflow = myForm.overflow
              }
            }
            this.getData()
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
    },
    watch:{
      'item.api'(){
        this.getData()
      }
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .el-icon-setting {
    position: absolute;
    top: 5px;
    right: 25px;
    z-index: 999;
    &:hover {
      cursor: pointer !important;
    }
  }
</style>



