<template>
  <div>
    <i class="el-icon-setting" @click="setThisCom(item)" v-if="iconShow"></i>
    <el-button :type="item.buttonConfig.type" @click="btnClick.click(item)">
      <!--<a :href="item.link" target="_blank"></a>-->
      {{item.buttonConfig.buttonText}}
    </el-button>
    <el-button>
      <a :href="item.link" target="_blank">带连接的按钮</a>
    </el-button>
    <el-dialog title="buttonSet" :visible.sync="dialogFormVisible" append-to-body>
      <el-form :model="myForm" ref="myForm" label-width="100px">
        <form-public :myForm="myForm"></form-public>
        <el-form-item label="api"
                      prop="api">
          <el-input v-model="myForm.api" auto-complete="off" placeholder="api"></el-input>
        </el-form-item>
        <el-form-item label="link"
                      prop="link">
          <el-input v-model="myForm.link" auto-complete="off" placeholder="link"></el-input>
        </el-form-item>
        <el-form-item label="sql"
                      prop="sql">
          <el-input v-model="myForm.sql" auto-complete="off" placeholder="sql"></el-input>
        </el-form-item>
        <el-form-item label="buttonText"
                      prop="buttonText">
          <el-input v-model="myForm.buttonText" auto-complete="off" placeholder="buttonText"></el-input>
        </el-form-item>
        <el-form-item label="type"
                      prop="type"
                      :rules="[{required:true,message: '必须输入'}]">
          <el-select v-model="myForm.type" placeholder="请选择">
            <el-option value="default"></el-option>
            <el-option value="success"></el-option>
            <el-option value="warning"></el-option>
            <el-option value="primary"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="target"
                      prop="target">
          <el-input v-model="myForm.target" auto-complete="off" placeholder="target"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="sureSetCom(myForm)">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import store from '@/store'
  import axios from 'axios'

  export default {
    name: "myButton",
    props: ['item'],

    data() {
      const self = this
      this.btnClick = {
        click: function (item) {
          const id = item.buttonConfig.target
          for (let i in self.dragAndResizeArr) {
            if (self.dragAndResizeArr[i].id === id) {
              if (self.dragAndResizeArr[i].api === '/chartsLine1') {
                self.dragAndResizeArr[i].api = '/chartsLine0'
              } else {
                self.dragAndResizeArr[i].api = '/chartsLine1'
              }
            }
          }
        }
      }
      return {
        comId: '',//组件id
        iconShow: this.$route.path === '/dragAndResize',
        rules: [
          {type: 'number', message: '必须为数字值'}
        ],
        dialogFormVisible: false,//显示dialog
        myForm: {
          overflow: 'hidden',
          type: 'default',
          // buttonText: '按钮',
          // link: 'javascript:;',
        },//表单
      }
    },
    computed: {
      dragAndResizeArr() {
        return store.getters.dragAndResizeArr
      }
    },
    methods: {
      setThisCom(item) {//点击设置的时候显示数据
        this.comId = item.id
        this.dialogFormVisible = true
        //    ------单独配置  开始
        this.myForm.type = item.buttonConfig.type
        this.myForm.buttonText = item.buttonConfig.buttonText
        this.myForm.target = item.buttonConfig.target
        //    ------单独配置  结束

        this.myForm.link = item.link
        this.myForm.sql = item.sql
        for (let i in this.dragAndResizeArr) {
          if (this.dragAndResizeArr[i].id === item.id) {
            this.myForm.height = Number(this.dragAndResizeArr[i].objStyle.height.replace('px', ''))
            this.myForm.width = Number(this.dragAndResizeArr[i].objStyle.width.replace('px', ''))
            this.myForm.top = Number(this.dragAndResizeArr[i].objStyle.top.replace('px', ''))
            this.myForm.left = Number(this.dragAndResizeArr[i].objStyle.left.replace('px', ''))
            this.myForm.overflow = this.dragAndResizeArr[i].objStyle.overflow
            this.myForm.border = this.dragAndResizeArr[i].objStyle.border
            this.myForm.id = this.dragAndResizeArr[i].id
          }
        }
      },
      sureSetCom(myForm) {
        this.$refs['myForm'].validate((valid) => {
          if (valid) {
            this.dialogFormVisible = false
            //-------------- 单独配置 开始
            this.item.buttonConfig.type = myForm.type
            this.item.buttonConfig.buttonText = myForm.buttonText
            this.item.buttonConfig.target = myForm.target
            //-------------- 单独配置 结束

            this.item.api = myForm.api
            this.item.link = myForm.link
            this.item.sql = myForm.sql
            for (let i in this.dragAndResizeArr) {
              if (this.dragAndResizeArr[i].id === this.comId) {
                this.dragAndResizeArr[i].objStyle.height = myForm.height + 'px'
                this.dragAndResizeArr[i].objStyle.width = myForm.width + 'px'
                this.dragAndResizeArr[i].objStyle.top = myForm.top + 'px'
                this.dragAndResizeArr[i].objStyle.left = myForm.left + 'px'
                this.dragAndResizeArr[i].objStyle.overflow = myForm.overflow
              }
            }
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .el-icon-setting {
    position: absolute;
    top: 5px;
    right: 25px;
    z-index: 999;
    &:hover {
      cursor: pointer !important;
    }
  }
</style>



