<template>
  <div>
    <el-row>
      <el-col :span="3">
        <div class="buttonContainer">
          <el-button @click="goTo()">预览</el-button>
        </div>
        <!--<div v-for="item in navArr"-->
        <!--v-Resizable="{disabled:resizable,edge:edge}"-->
        <!--v-Draggable="{disabled:draggable,dragEnd:dragEnd,dragStart:dragStart}"-->
        <!--@mousemove="onMouseMove($event,item)"-->
        <!--:style="dragStyle">-->
        <!--<div style="padding:20px;text-align:center">{{item}}</div>-->
        <!--</div>-->
        <div v-for="comName in navArr" :style="dragStyle" @mousedown="leftOnMouseDown(comName)">{{comName}}</div>
      </el-col>
      <!--<el-col :span="4">-->

        <!--<div>-->
      <!--<pre>-->
<!--{{dragAndResizeArr}}-->
      <!--</pre>-->
        <!--</div>-->
      <!--</el-col>-->
      <el-col :span="21">
        <div style="min-height:2000px;background-color: #DDDDDD;position:relative;">
          <div v-for="(item,index) in dragAndResizeArr"
               v-Resizable="{disabled:resizable,edge:edge,resizeStop:resizeStopStop}"
               v-Draggable="{disabled:draggable,dragEnd:dragEndEnd,dragStart:dragStartStart}"
               @mousemove="onMouseMove($event,item,index)"
               @mouseover="onMouseOver($event,item,index)"
               :style="item.objStyle">
            <!--padding-right:20px;padding-bottom:20px;-->
            <div style="position: relative;">
              <i class="el-icon-remove" @click="delCom(item)"></i>
              <component :is="item.name" :item="item"></component>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import {newGuid} from './guid'
  import componentAll from './importComponents'
  import store from '@/store'

  export default {
    name: "dragAndResize",
    components: {
      ...componentAll
    },
    data() {
      return {
        itemObj: {},
        objStyle: {},
        hasMove: false,
        isDrag: false,
        navArr: this.getNav(),
        // dragAndResizeArr: [],
        resizable: true,
        draggable: true,
        edge: 20,
        dragStyle: {
          background: '#dddd',
          border: '1px solid #000',
          height: '50px',
          width: '200px'
        }
      }
    },
    created() {
      // this.getNav()
    },
    computed: {
      dragAndResizeArr() {
        return store.getters.dragAndResizeArr
      }
    },
    methods: {
      getNav() {
        const arr = []
        for (let i in componentAll) {
          const obj = {}
          obj[i] = {}
          arr.push(i)
        }
        return arr
      },
      delCom(item) {
        for (var i in this.dragAndResizeArr) {
          if (this.dragAndResizeArr[i].id === item.id) {
            this.dragAndResizeArr.splice(i, 1)
          }
        }
      },
      onMouseOver(e, item, index) {
        const obj = {
          id: item.id,
          name: item.name
        }
        this.itemObj = obj
      },
      goTo() {
        this.$store.dispatch('setDragAndResizeArrActions', this.dragAndResizeArr)
        this.$router.push('/dragAndResizePreview')
      },
      leftOnMouseDown(comName) {
        const objPublic = {
          id: newGuid(),
          secondId: '',
          name: comName,
          api: '',
          link: 'https://www.baidu.com',
          sql: '',
          objStyle: {
            height: 300 + 'px',
            width: 300 + 'px',
            left: 0 + 'px',
            top: 0 + 'px',
            position: 'absolute',
            border: '1px solid #000',
            overflow: 'hidden',
          },
        }
        const objPrivate = {}
        if (comName.toLowerCase().indexOf('charts') > -1) {
          objPrivate.options = { // 针对图表的配置
            colors: ['#c23531', '#2f4554', '#61a0a8', '#d48265', '#91c7ae', '#749f83', '#ca8622', '#bda29a', '#6e7074', '#546570', '#c4ccd3'],
            chartSettings: {type: 'line'},
            clickFunction: ''
          }
        } else if (comName.toLowerCase().indexOf('button') > -1) {
          objPrivate.buttonConfig = { // 针对button的配置
            type: 'default',
            buttonText: '按钮',
            target: ''
          }
        }
        const pushObj = {...objPublic, ...objPrivate}
        this.dragAndResizeArr.push(pushObj)
      },
      dragStartStart() {
        this.isDrag = true
      },
      resizeStopStop(e) {
        this.objStyle = {
          height: e.height + 'px',
          width: e.width + 'px',
          left: e.left + 'px',
          top: e.top + 'px',
          position: 'absolute',
          border: '1px solid #000',
          overflow: 'hidden',
        }
        for (var i in this.dragAndResizeArr) {
          if (this.dragAndResizeArr[i].id === this.itemObj.id) {
            this.dragAndResizeArr[i].objStyle.height = this.objStyle.height
            this.dragAndResizeArr[i].objStyle.width = this.objStyle.width
            this.dragAndResizeArr[i].objStyle.left = this.objStyle.left
            this.dragAndResizeArr[i].objStyle.top = this.objStyle.top
          }
        }
        this.$store.dispatch('setDragAndResizeArrActions', this.dragAndResizeArr)
      },
      dragEndEnd(e) {
        this.objStyle = {
          height: e.height + 'px',
          width: e.width + 'px',
          left: e.left + 'px',
          top: e.top + 'px',
          position: 'absolute',
          border: '1px solid #000',
          overflow: 'hidden',
        }
        for (var i in this.dragAndResizeArr) {
          if (this.dragAndResizeArr[i].id === this.itemObj.id) {
            this.dragAndResizeArr[i].objStyle.height = this.objStyle.height
            this.dragAndResizeArr[i].objStyle.width = this.objStyle.width
            this.dragAndResizeArr[i].objStyle.left = this.objStyle.left
            this.dragAndResizeArr[i].objStyle.top = this.objStyle.top
          }
        }
        this.$store.dispatch('setDragAndResizeArrActions', this.dragAndResizeArr)
        this.hasMove = true
        this.isDrag = false
      },
      onMouseMove(e, item, index) {
        if (Math.abs(e.offsetX - e.currentTarget.offsetWidth) <= 20) {
          this.resizable = false
          this.draggable = true
        } else if (Math.abs(e.offsetY - e.currentTarget.offsetHeight) <= 20) {
          this.resizable = false
          this.draggable = true
        } else {
          this.resizable = true
          this.draggable = false
        }
      },
      onDrag() {
        console.log('正在拖拽')
      }
    },
    watch: {
      isDrag() {
        this.navArr = this.getNav()
      }
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>

  .el-icon-remove {
    position: absolute;
    top: 5px;
    right: 5px;
    z-index: 999;
    &:hover {
      cursor: pointer !important;
    }
  }

  .item {
    background-color: #3a87ad;
    border: 1px solid #000000;
  }
</style>



